<template>
  <div>
    <mt-button class="myOrder" plain type="primary" size="large" @click.native="myOrder">我的预定</mt-button>
    <mt-button class="picker" @click="openPicker" type="primary" size="small">日期选择</mt-button>
    <span class="shijianduan0">{{ this.formatTime }}</span>
    </br>
    <mt-button class="time" type="primary" size="small" @click.native="change">时间选择</mt-button>
    <span class="shijianduan1">{{ this.shijianduan }}</span>
    <mt-datetime-picker ref="picker" v-model="pickerValue" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日" :startDate="startDate" @confirm="handleConfirm">
    </mt-datetime-picker>
    <p class="roomTitle">整房：</p>
    <ul class="fangjian">
      <li v-for="(fangjian, index) in zhengzuRoom" @click="push1(index)">
        <span class="roomInfo">
          <img class="picture" :src="fangjian.picUrl">
          <p class="room">房间{{ fangjian.no }} <small class="small">价格:{{ fangjian.price }}</small></p>
          <p class="numbers">{{ fangjian.current }}/{{ fangjian.total }}</p>
        </span>
      </li>
    </ul>
    <p class="roomTitle">散房：</p>
    <ul class="fangjian">
      <li v-for="(fangjian, index) in sanzuRoom" @click="push0(index)">
        <span class="roomInfo">
          <img class="picture" :src="fangjian.picUrl">
          <p class="room">房间{{ fangjian.no }} <small class="small">价格:{{ fangjian.price }}</small></p>
          <p class="numbers">{{ fangjian.current }}/{{ fangjian.total }}</p>
        </span>
      </li>
    </ul>
  </div>
</template>
<script type="text/javascript">
import { DatetimePicker } from 'mint-ui'
import { Toast } from 'mint-ui'
import axios from 'axios'

export default {
  name: 'fangjian',
  data() {
    return {
      shijianduan: '13:00-16:00',
      shijianduanId: 1,
      zhengzuRoom: [],
      sanzuRoom: [],
      pickerValue: new Date(),
      startDate: new Date(),
    }
  },
  computed: {
    formatTime() {
      return `${this.pickerValue.getFullYear()}-${this.pickerValue.getMonth() + 1}-${this.pickerValue.getDate()}`
    }
  },
  created() {
    // type = 0, 散房, 时间段默认查第一个
    axios.post('http://shine.tunnel.qydev.com/user/room', {
        date: this.formatTime,
        phase: this.shijianduanId,
        type: 0,
      })
      .then(response => {
        // console.log(response)
        this.sanzuRoom = response.data
      })
      .catch(error => {
        alert(error)
      })

    // type = 1, 整租房
    axios.post('http://shine.tunnel.qydev.com/user/room', {
        date: this.formatTime,
        phase: this.shijianduanId,
        type: 1,
      })
      .then(response => {
        // console.log(response)
        this.zhengzuRoom = response.data
      })
      .catch(error => {
        alert(error)
      })
  },
  methods: {
    // 设置时间段id
    change() {
      if (this.shijianduan == '13:00-16:00') {
        this.shijianduan = '16:00-19:00'
        this.shijianduanId = 2
        // alert(this.shijianduanId)
      } else if (this.shijianduan == '16:00-19:00') {
        this.shijianduan = '19:00-22:00'
        this.shijianduanId = 3
      } else if (this.shijianduan == '19:00-22:00') {
        this.shijianduan = '22:00-24:00'
        this.shijianduanId = 4
      } else if (this.shijianduan == '22:00-24:00') {
        this.shijianduan = '13:00-16:00'
        this.shijianduanId = 1
      }
      this.getRoomInfo()
    },
    openPicker() {
      this.$refs.picker.open()
    },
    handleConfirm() {
      // console.log(typeof(this.formatTime))
      this.getRoomInfo()
    },
    // 转到我的订单
    myOrder() {
      this.$router.push('/myorder')
    },
    // 转到房间页面，需要的参数有房间id，时间段，日期, 0-散, 1-整
    push0(index) {
      // console.log(this.sanzuRoom[index].no)
      if (this.sanzuRoom[index].current == this.sanzuRoom[index].total) {
        Toast({
          message: '该房间已满，请您选择其他房间^^'
        })
      } else {
        this.$router.push({
          name: 'zuowei_sanjian',
          params: {
            roomId: this.sanzuRoom[index].no,
            date: this.formatTime,
            phase: this.shijianduanId,
            price: this.sanzuRoom[index].price,
            profile: this.sanzuRoom[index].profile,
          }
        })
      }
    },
    push1(index) {
      // console.log()
      if (this.zhengzuRoom[index].current == this.zhengzuRoom[index].total) {
        Toast({
          message: '该房间已满，请您选择其他房间^^'
        })
      } else {
        this.$router.push({
          name: 'zuowei_zheng',
          params: {
            roomId: this.zhengzuRoom[index].no,
            date: this.formatTime,
            phase: this.shijianduanId,
            price: this.zhengzuRoom[index].price,
            profile: this.zhengzuRoom[index].profile,
            fangdongNumber: this.zhengzuRoom[index].current,
          }
        })
      }
    },
    // 获取房间信息函数，每次点击时间段和改变日期都会触发,type = 1, 散间
    getRoomInfo() {
      // alert('getRoomInfo')
      axios.post('http://shine.tunnel.qydev.com/user/room', {
          date: this.formatTime,
          phase: this.shijianduanId,
          type: 0,
        })
        .then(response => {
          // console.log(this.formatTime)
          // console.log(this.shijianduanId)
          this.sanzuRoom = response.data
        })
        .catch(error => {
          alert(error)
        })

      // type = 1, 整租房
      axios.post('http://shine.tunnel.qydev.com/user/room', {
          date: this.formatTime,
          phase: this.shijianduanId,
          type: 1,
        })
        .then(response => {
          // console.log(response)
          // console.log(this.formatTime)
          // console.log(this.shijianduanId)
          this.zhengzuRoom = response.data
        })
        .catch(error => {
          alert(error)
        })
    },
  }
}

</script>
<style type="text/css" scoped>
* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  margin-top: 3%;
  /*text-align: center;*/
  font-size: 1em;
}

.fangjian li {
  margin: 3% 4%;
  /*border-bottom: 1px solid #708090;*/
  border-radius: 10%;
  background-color: white;
  cursor: pointer;
}

.picture {
  height: 3em;
  width: 3em;
  /*line-height: 3em;*/
  border-radius: 15%;
  padding: 3% 5% 3% 15%;
  float: left;
  position: relative;
}

.roomInfo {
  display: inline-block;
}

.room {
  padding: 5% 0 0 0;
  font-size: 1em;
}

.numbers {
  padding: 5% 5% 0 200px;
  color: #9C9C9C;
}

.picker {
  margin: 1% 20% 0 15%;
}

.time {
  margin: 1% 20% 0 15%;
}

.roomTitle {
  margin: 3% 0 0 5%;
  font-size: 1.2em;
}

.myOrder {
  width: 90%;
  margin: 5%;
}

.small {
  font-size: .3em;
  color: #666666;
  border-radius: 50%;
}

</style>



