<template>
  <div  class="background">
    <!-- <div class="block">教练介绍</div> -->
      <p class="jieshao">点击图片评论他／她</p>
      <div class="box">
        <button class="btn prev" @click="prev"><</button>
        <button class="btn next" @click="next ">></button>
        <img :src=teacher.imgUrl class="pic" @click="push" alt="加载中。。。">
        <h4 class="name">{{ this.teacher.name }}</h4>
      </div>
  </div>
</template>
<script type="text/javascript">
import axios from 'axios'
import store from '@/vuex/store'

export default {
  name: 'jiaolian',
  data() {
    return {
      teachers: [],
      teacher: {},
    }
  },
  created() {
    axios.get('http://shine.tunnel.qydev.com/user/teacher')
      .then(response => {
        // console.log(response.data)
        this.teachers = response.data
        this.teacher = this.teachers[0]
        this.loadPic()
      })
      .catch(error => {
        alert(error)
      })
    // this.teachers = [{
    //   'pic': 'http://123.206.218.163/picture/1.jpg',
    //   'name': 'a',
    //   'id': 1,
    // }, {
    //   'pic': 'http://123.206.218.163/picture/2.jpg',
    //   'name': 'b',
    //   'id': '2',
    // }, {
    //   'pic': 'http://123.206.218.163/picture/3.jpg',
    //   'name': 'c',
    //   'id': 3,
    // }]
  },
  methods: {
    prev() {
      // console.log(this.teacher)
      let index
      for (var i = 0; i < this.teachers.length; i++) {
        if(this.teacher == this.teachers[i]) {
          index = i
        }
      }
      if (index == 0) {
        index = this.teachers.length - 1
        this.teacher = this.teachers[index]
        this.loadPic()
      } else {
        index -= 1
        this.teacher = this.teachers[index]
        this.loadPic()
      }
    },
    next() {
      let index
      for (var i = 0; i < this.teachers.length; i++) {
        if(this.teacher == this.teachers[i]) {
          index = i
        }
      }
      // console.log(index)
      if (index == this.teachers.length - 1) {
        index = 0
        this.teacher = this.teachers[index]
        this.loadPic()
      } else {
        index += 1
        this.teacher = this.teachers[index]
        // console.log(index)
        this.loadPic()
      }
    },
    // 跳转到评论区
    push() {
      this.$router.push({name: 'commit', params: { id: this.teacher.id }})
    },
    // 加载默认图片
    loadPic() {
      if (this.teacher.imgUrl == null ) {
          this.teacher.imgUrl = "http://123.206.218.163/picture/1.jpg"
        }
    }
  },
  store
}

</script>
<style type="text/css" scoped>
* {
  margin: 0;
  padding: 0;
}

.background {
  height: 100%;
  width: 100%;
  /*background-color: #BCBCBC;*/
  background: url('https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1504271163613&di=f299b9bcdd53d0eee9f14ef79c878b00&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F01742658fdbe85a8012160f7965a66.png%40900w_1l_2o_100sh.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-color: black；

}

.box {
  width: 100%;
  height: 500px;
  margin: 1% auto;/*
  border-radius: 15px;*/
}

.btn {
  height: 25%;
  width: 15%;
  background-color: white;
  /*border-radius: 20%;*/
  cursor: pointer;
}

.pic {
  width: 80%;
  height: 85%;
  margin: 10%;
  /*border-radius: 10%;*/
}

.name {
  font-size: 1.5em;
  margin: 0 auto;
  color: white;
  text-align: center;
}

.prev {
  position: absolute;
  left: 0;
  top: 35%;
  background: gray;
}

.next {
  position: absolute;
  right: 0;
  top: 35%;
  background: gray;
}

.jieshao {
  margin: 1% 0 0 5%;
}

</style>



